body {
  margin: 0 auto;
  font-family: Roboto, sans-serif;
}

.main-container {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), auto);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px 72px;
  display: grid;
}

@media (width >= 488px) {
  .main-container {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .main-container {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .main-container {
    --column-count: 4;
  }
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  height: 40px;
}

.header__nav .nav__hamburguer:first-child {
  background: url("icon-burger-menu.9c919167.svg") 50% / contain no-repeat;
  width: 32px;
  height: 32px;
  transition: all .3s;
  display: flex;
}

@media (width >= 830px) {
  .header__nav .nav__hamburguer:first-child {
    display: none;
  }
}

.nav__list {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
}

@media (width >= 830px) {
  .nav__list {
    display: flex;
  }
}

.nav__list:target {
  display: flex;
}

.nav__item {
  position: relative;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 16px;
  transition: all .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  align-self: center;
  width: 160px;
  height: 134px;
  margin-top: 16px;
  margin-bottom: 40px;
}

.card__button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  padding: 11px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.card__title-product {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__title-product {
  color: #34568b;
}

.card__product-code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  font-style: normal;
  line-height: 14px;
}

.card__price {
  color: #060b35;
  margin: 0;
  font-weight: 700;
  line-height: 18px;
}

.card__price-content {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  margin: 0;
  font-size: 12px;
}

.card__stars {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.stars__review {
  display: flex;
}

.stars__review-star {
  box-sizing: border-box;
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__review--stars--5 .stars__review-star, .stars__review--stars--4 .stars__review-star:nth-child(-n+4), .stars__review--stars--3 .stars__review-star:nth-child(-n+3), .stars__review--stars--2 .stars__review-star:nth-child(-n+2), .stars__review--stars--1 .stars__review-star:nth-child(-n+1) {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.stars__text-review {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

